const html = "<p class=\"MsoNormal\" align=\"center\">Expediente (Aqu&iacute; va el n&uacute;mero de expediente)</p> <p class=\"MsoNormal\" align=\"center\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"center\">AUTO No. 120-39.2-(Aqu&iacute; va el No. Consecutivo de auto inhibitorio)</p> <p class=\"MsoNormal\" align=\"center\">&nbsp;</p> <p class=\"MsoNormal\" align=\"center\">(Mediante el cual se Inhibe de iniciar actuaci&oacute;n disciplinaria)</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Santiago de Cali,</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">EL DIRECTOR(A) DE CONTROL INTERNO DISCIPLINARIO DE EMCALI E.I.C.E ESP, EN EJERCICIO DE LAS ATRIBUCIONES QUE LE CONFIERE LA LEY 1952 &nbsp;DE 2019, ORDENA INHIBIRSE DE INICIAR ACTUACION DISCIPLINARIA. &nbsp;</p> <p class=\"MsoNormal\" align=\"justify\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"center\">FINALIDAD DE LA DECISI&Oacute;N:</p> <p class=\"MsoNormal\" align=\"justify\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"justify\">Por medio de este prove&iacute;do, entramos a resolver sobre la queja formulada por el se&ntilde;or <u>nombre solicitante</u>&nbsp;con fecha <u>d&iacute;a</u>&nbsp;de <u>mes</u>&nbsp;de <u>a&ntilde;o</u>&nbsp;recepcionada en ventanilla &uacute;nica de EMCALI E.I.C.E. ESP. &nbsp;&nbsp;Y con numero de radicaci&oacute;n <u>N&uacute;mero de radicado de ventanilla &uacute;nica</u>, donde se aportaron____anexos, &nbsp;para lo de nuestra competencia.</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">En la queja o informe, el se&ntilde;or -<u>&nbsp;nombre solicitante</u>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;en su calidad de <u>Calidad del solicitante</u>, &nbsp;hace alusi&oacute;n &nbsp;a que el funcionario <u>Nombre completo del funcionario</u>___, obrando como <u>Calidad del solicitante</u>&nbsp;&nbsp;&nbsp;, al contestar el oficio</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\"><em>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;SITUACI&Oacute;N FACTICA Y JUR&Iacute;DICA:</em></p> <p class=\"MsoNormal\" align=\"center\"><em>&nbsp;</em></p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">La &nbsp;queja formulada por el se&ntilde;or _<u>&nbsp;nombre solicitante</u>&nbsp;________________,</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;&nbsp;</p> <p class=\"MsoNormal\"><em>&nbsp;</em></p> <p class=\"MsoNormal\" align=\"center\"><em>CONSIDERACIONES DEL DESPACHO: </em></p> <p class=\"MsoNormal\" align=\"center\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Encuentra el Despacho, que en la queja formulada por el se&ntilde;or <u>nombre solicitante</u> relaciona que la expresi&oacute;n, palabra &nbsp;o t&eacute;rmino utilizado &nbsp;por el funcionario <u>Nombre completo del funcionario.</u></p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Verificado en el Diccionario&nbsp;Manual de la Lengua Espa&ntilde;ola Vox. &copy; 2007 Larousse Editorial, S.L., el significado de INDIVIDUO, encontramos que corresponde a:</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\"><em>&nbsp;</em></p> <p class=\"MsoNormal\"><!-- [if !supportLists]--><span style=\"mso-list: Ignore;\">1&nbsp;</span><!--[endif]--><em>Persona perteneciente a una clase o grupo, considerada independientemente de las dem&aacute;s. </em></p> <p class=\"MsoNormal\"><em>&nbsp;</em></p> <p class=\"MsoNormal\" align=\"justify\"><!-- [if !supportLists]--><span style=\"mso-list: Ignore;\">2&nbsp;</span><!--[endif]--><em>Persona cuya identidad se desconoce o no se expresa: </em><em><span class=\"17\">tres individuos armados perpetraron el atraco a la entidad bancaria. </span></em><em><span class=\"15\">Tipo. </span></em></p> <p class=\"MsoNormal\"><em>&nbsp;</em></p> <p class=\"MsoNormal\" align=\"justify\"><!-- [if !supportLists]--><span style=\"mso-list: Ignore;\">3&nbsp;</span><!--[endif]--><em>Ser vivo, animal o vegetal, perteneciente a una especie, considerado independientemente de los dem&aacute;s: </em><em><span class=\"17\">la caza incontrolada del lince ib&eacute;rico ha provocado que actualmente existan muy pocos individuos en libertad.</span></em></p> <p class=\"MsoNormal\"><em><span class=\"17\">&nbsp;</span></em></p> <p class=\"MsoNormal\"><!-- [if !supportLists]--><span style=\"mso-list: Ignore;\">4&nbsp;</span><!--[endif]--><em>Elemento que forma parte de la poblaci&oacute;n sobre la que se toma una muestra </em></p> <p class=\"MsoNormal\"><em>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;estad&iacute;stica.</em></p> <p class=\"MsoNormal\"><em>&nbsp;</em></p> <p class=\"MsoNormal\">&nbsp;</p> <p class=\"MsoNormal\">De igual forma revisado el libro de sin&oacute;nimos y ant&oacute;nimos, tenemos que el sin&oacute;nimo del t&eacute;rmino o palabra &nbsp;&nbsp;&ldquo;INDIVIDUO&rdquo; &nbsp;corresponde a: &ldquo;Persona, sujeto, personaje, humano&hellip;&rdquo;, etc.</p> <p class=\"MsoNormal\">&nbsp;</p> <p class=\"MsoNormal\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Para el Despacho, el t&eacute;rmino utilizado &nbsp;por el funcionario <u>Nombre completo del funcionario</u></p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">En trat&aacute;ndose de los efectos del art&iacute;culo 209 de la ley 1952 de 2019 y ante la presencia de los presupuestos legales all&iacute; establecidos, le asiste al Operador Disciplinario la posibilidad legal de abstenerse de conocer de un determinado asunto, lo que implica no ejercer una atribuci&oacute;n o una facultad que le es propia para la decisi&oacute;n inhibitoria que presupone la existencia de una queja temeraria, hechos irrelevantes en el campo del derecho disciplinario, casos de imposible ocurrencia o presentados de forma inconcreta o difusa. &nbsp;&nbsp;</p> <p class=\"MsoNormal\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Art. 209. <em><span style=\"font-family: Arial;\">&ldquo;Decisi&oacute;n inhibitoria. Cuando la informaci&oacute;n o queja sea manifiestamente temeraria o se refiera a hechos disciplinariamente irrelevantes o de imposible ocurrencia o sean presentados de manera absolutamente inconcreta o difusa, o cuando la acci&oacute;n no puede iniciarse, el funcionario de plano se inhibir&aacute; de iniciar actuaci&oacute;n alguna. Contra esta decisi&oacute;n no procede recurso.&rdquo;</span></em></p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">En consecuencia y en m&eacute;rito de lo expuesto, el DIRECTOR(A) DE CONTROL INTERNO DISCIPLINARIO DE EMCALI E.I.C.E. ESP.</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"center\">R E S U E L V E:</p> <p class=\"MsoNormal\" align=\"center\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"justify\">PRIMERO:<strong>&nbsp;</strong>INHIBIRSE DE INICIAR ACTUACI&Oacute;N DISCIPLINARIA, sobre la queja formulada por el se&ntilde;or <u>nombre solicitante</u>&nbsp;_______, con fecha&nbsp;___ de _________ de ______, &nbsp;con base en la parte motiva de este prove&iacute;do. &nbsp;</p> <p class=\"MsoNormal\" align=\"justify\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"justify\">SEGUNDO:<strong>&nbsp;</strong>Comun&iacute;quese la presente decisi&oacute;n al quejoso(a). &nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">TERCERO: Contra la presente decisi&oacute;n no procede recurso por tratarse de una decisi&oacute;n de plano.</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">CUARTO: Disponer el archivo del expediente, previa anotaci&oacute;n en el libro radicador.</p> <h1>Comun&iacute;quese y C&uacute;mplase</h1> <p class=\"MsoNormal\" align=\"justify\"><strong>&nbsp;</strong></p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <p class=\"MsoNormal\" align=\"center\">Nombre del Director</p> <p class=\"MsoNormal\" align=\"center\">Director</p> <p class=\"MsoNormal\" align=\"center\">&nbsp;</p> <p class=\"MsoNormal\" align=\"justify\">Proyect&oacute;, Elabor&oacute;: Nombre funcionario asignado</p> <p class=\"MsoNormal\" align=\"justify\">Revisado, Firmado: &nbsp;Nombre del Director</p> <p class=\"MsoNormal\" align=\"justify\">&nbsp;</p> <table class=\"MsoNormalTable\" border=\"0\" cellspacing=\"0\"> <tbody> <tr> <td valign=\"top\" width=\"131\"> <p class=\"MsoNormal\">&nbsp;</p> </td> <td valign=\"top\" width=\"100.0000%\"> <p class=\"MsoNormal\">&nbsp;</p> </td> </tr> </tbody> </table> <p class=\"MsoNormal\" align=\"justify\"><strong>&nbsp;</strong></p>"

