import { Box, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Tooltip } from '@mui/material';
//DataGrid import
import {
  DataGrid,
  GridActionsCellItem,
  GridRenderCellParams,
  /* GridToolbar, */
} from '@mui/x-data-grid';
import moment from 'moment';
import { useEffect, useState } from 'react';
import Swal from 'sweetalert2';

//SVG Icon
import iconSearch from '../../assets/images/svg/iconSearch.svg';
import iconEdit from '../../assets/images/svg/iconEdit.svg';
import iconLock from '../../assets/images/svg/iconLock.svg';
import iconLockOpen from '../../assets/images/svg/iconLockOpen.svg';
import iconPlus from '../../assets/images/svg/iconPlus.svg';
//Components
import { MainLoader, StateBadge, Titles } from '../../components';

//Services
import { usePermission } from '../../utilities';

// import { Auth } from "../../core/interfaces/Auth";
import useFetchAndLoad from '../../hooks/useFetchAndLoad';

/* import { getLegalDocs } from "../../services/positions.service"; */
import { createGlobalConfig, getAllPublicDefenders, getGlobalConfig, getTemplates } from '../../services';
import { ModalCreatePlantilla, ModalUpdatePlantillaState } from './components';

export const Plantillas = () => {
  const { callEndpoint } = useFetchAndLoad();

  const [searchText, setSearchText] = useState('');
  const [openModal, setOpenModal] = useState(false);
  const [dataSource, setDataSource] = useState<any>('');
  const [rows, setRows] = useState<any>(null);
  const [plantillaSelected, setPlantillaSelected] = useState<any>('');
  const [publicDefenderSaved, setPublicDefenderSaved] = useState(false);
  const [typeOfCRUDAction, setTypeOfCRUDAction] = useState('');
  const [state, setState] = useState('true');
  const [isLoading, setIsLoading] = useState(false);
  const [globalConfig, setGlobalConfig] = useState<any>(null);

  const canCreate = usePermission('public-defenders', 'canCreate');
  const canEdit = usePermission('public-defenders', 'canEdit');

  const showToast = (icon: any, title: any, text: any) => {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 5000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer);
        toast.addEventListener('mouseleave', Swal.resumeTimer);
      },
    });

    Toast.fire({
      icon: icon,
      title: title,
      text: text,
    });
  };

  useEffect(() => {
    const newData =
      dataSource &&
      dataSource.filter(
        (row: any) =>
          row.id.toString().toLocaleLowerCase().includes(searchText.toLocaleLowerCase()) ||
          row.templateName.toLocaleLowerCase().includes(searchText.toLocaleLowerCase()) ||
          row.templateStage?.stageName?.toLocaleLowerCase().includes(searchText.toLocaleLowerCase()),
      );
    setRows(newData);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchText, state]);

  const getPlantillas = async () => {
    try {
      setIsLoading(true);
      const response = await callEndpoint(getTemplates());
      //const response = await responseMock;
      response.success && setRows(response.data);
      response.success && setDataSource(response.data);
      setIsLoading(false);
    } catch (error) {
      showToast('error', 'Ocurri un error cargando la consulta', 'Contacte a soporte tcnico');
      setIsLoading(false);
    }
  };

  useEffect(() => {
    getPlantillas();
    callEndpoint(getGlobalConfig()).then((response) => {
      setGlobalConfig(response?.data);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [publicDefenderSaved]);

  const handleSearchInput = (e: any) => {
    setSearchText(e.target.value);
  };

  const handleSelectInput = (event: SelectChangeEvent) => {
    setState(event.target.value as string);
  };

  const handleOpenModal = (crudAction: any, positionSelected?: any) => {
    setTypeOfCRUDAction(crudAction);
    setPlantillaSelected(positionSelected);
    setOpenModal(!openModal);
  };

  const columns = [
    {
      field: 'actions',
      headerName: 'Acciones',
      type: 'actions',
      minWidth: 200,
      getActions: (params: GridRenderCellParams) => [
        <GridActionsCellItem
          className="bg-transparent"
          disabled={!canEdit}
          icon={
            <Tooltip title="Ver/Editar">
              <img src={iconEdit} className="" alt="buscar un cargo" />
            </Tooltip>
          }
          onClick={(e: any) => {
            e.preventDefault();
            handleOpenModal('edit', params.row);
          }}
          label="View/Edit"
        />,
        <GridActionsCellItem
          disabled={!canEdit}
          icon={
            <Tooltip
              title={params.row.publicDefenderState ? 'Bloquear Abogado de Oficio' : 'Desbloquear Abogado de Oficio'}
            >
              {params.row.publicDefenderState ? (
                <img src={iconLockOpen} className="" alt="Actualizar estado del abogado de oficio" />
              ) : (
                <img src={iconLock} className="" alt="Actualizar estado del abogado de oficio" />
              )}
            </Tooltip>
          }
          onClick={(e: any) => {
            e.preventDefault();
            handleOpenModal('changeState', params.row);
          }}
          label="Actualizar estado del abogado de oficio"
        />,
      ],
    },
    {
      field: 'requestState',
      headerName: 'Estado',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) => params?.value?.stateName,
    },
    {
      field: 'templateName',
      headerName: 'Nombre de la plantilla',
      width: 520,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
    },
    {
      field: 'order',
      headerName: 'Orden',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) => params?.value || '99',
    },
    {
      field: 'consecutive',
      headerName: 'Consecutivo',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
    },
    {
      field: 'documentType',
      headerName: 'Tipo de documento',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
    },
    {
      field: 'onBase',
      headerName: 'Sistema Gestin Documental',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) => 'Si',
    },
    {
      field: 'isVario',
      headerName: 'Es vario',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) =>
        params?.value ? <input type="checkbox" checked disabled /> : <input type="checkbox" disabled />,
    },
    {
      field: 'seComunicaQuejoso',
      headerName: 'Comunica a quejoso',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) =>
        params?.value ? <input type="checkbox" checked disabled /> : <input type="checkbox" disabled />,
    },
    {
      field: 'seComunicaDisciplinado',
      headerName: 'Comunica a disciplinado',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) =>
        params?.value ? <input type="checkbox" checked disabled /> : <input type="checkbox" disabled />,
    },
    {
      field: 'seNotificaQuejoso',
      headerName: 'Notifica a quejoso',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) =>
        params?.value ? <input type="checkbox" checked disabled /> : <input type="checkbox" disabled />,
    },
    {
      field: 'seNotificaDisciplinado',
      headerName: 'Notifica a disciplinado',
      width: 320,
      cellClassName: 'MuiDataGrid-cell--textCenter',
      headerClassName: 'super-app-theme--header',
      renderCell: (params: GridRenderCellParams) =>
        params?.value ? <input type="checkbox" checked disabled /> : <input type="checkbox" disabled />,
    },
  ];

  return (
    <div>
      {/*Title Component*/}{' '}
      <Titles title="Plantillas" moduleDescription="Modulo de administracin de plantillas.">
        {canCreate && (
          <button
            onClick={() => handleOpenModal('create')}
            className="flex justify-center items-center py-2 px-8 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-primary hover:bg-primary-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-400"
          >
            <img src={iconPlus} className="mr-4" alt="Crear nuevo abogado de oficio" />
            Nueva plantilla
          </button>
        )}

        {/* Modal create */}
        {openModal && canEdit && canCreate && (typeOfCRUDAction === 'edit' || typeOfCRUDAction === 'create') && (
          <ModalCreatePlantilla
            openModal={openModal}
            setOpenModal={setOpenModal}
            plantillaSelected={plantillaSelected}
            setPublicDefenderSaved={setPublicDefenderSaved}
            publicDefenderSaved={publicDefenderSaved}
            typeOfCRUDAction={typeOfCRUDAction}
          />
        )}
        {/* Modal change state */}
        {openModal && canEdit && typeOfCRUDAction === 'changeState' && (
          <ModalUpdatePlantillaState
            openModal={openModal}
            setOpenModal={setOpenModal}
            plantillaSelected={plantillaSelected}
            setPublicDefenderSaved={setPublicDefenderSaved}
            publicDefenderSaved={publicDefenderSaved}
            typeOfCRUDAction={typeOfCRUDAction}
          />
        )}
      </Titles>
      {/*Search tab*/}{' '}
      <div className="grid grid-flow-col ml-27 mr-27 mt-29 bg-white w-auto rounded filter-container">
        <div className="mr-5">
          <Box>
            <TextField
              id="demo-helper-text-aligned"
              label="Search"
              onChange={handleSearchInput}
              className="w-full"
              size="small"
              InputProps={{
                startAdornment: <img src={iconSearch} className="mr-4" alt="Buscar abogado de oficio" />,
              }}
            />
          </Box>
        </div>
        {/* Select */}
        <div>
          <Box sx={{ flexGrow: 2 }}>
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Estado</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={state}
                label="Estado"
                onChange={handleSelectInput}
                size="small"
              >
                <MenuItem value={'true'}>Activo</MenuItem>
                <MenuItem value={'false'}>Inactivo</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </div>
      </div>
      <div className="grid grid-flow-col ml-27 mr-27 mt-4 bg-white w-auto rounded-md shadow-sm  gap-4 items-center p-7">
        <div className="">
          <Box>
            <label className="my-2 text-gray-500">Prefijo</label>
            <TextField
              id="demo-helper-text-aligned"
              onChange={(e) => {
                setGlobalConfig({ ...globalConfig, prefix: e.target.value });
              }}
              value={globalConfig?.prefix}
              className="w-full"
              size="small"
              // InputProps={{
              //   startAdornment: <img src={iconSearch} className="mr-4" alt="Buscar abogado de oficio" />,
              // }}
            />
          </Box>
        </div>
        {/* Select */}
        <div>
          <Box sx={{ flexGrow: 2 }}>
            <label className="my-2 text-gray-500">Autovario</label>
            <TextField
              id="demo-helper-text-aligned"
              // label="Autovario"
              value={globalConfig?.variosConsecutive}
              onChange={(e) => {
                setGlobalConfig({ ...globalConfig, variosConsecutive: e.target.value });
              }}
              type="number"
              className="w-full"
              size="small"
              // InputProps={{
              //   startAdornment: <img src={iconSearch} className="mr-4" alt="Buscar abogado de oficio" />,
              // }}
            />
          </Box>
        </div>
        <div>
          <Box sx={{ flexGrow: 2 }}>
            <label className="my-2 text-gray-500">Consecutivo Expediente</label>
            <TextField
              id="demo-helper-text-aligned"
              // label="Autovario"
              // value={globalConfig?.variosConsecutive}
              // onChange={(e) => {
              //   setGlobalConfig({ ...globalConfig, variosConsecutive: e.target.value });
              // }}
              type="number"
              className="w-full"
              size="small"
              // InputProps={{
              //   startAdornment: <img src={iconSearch} className="mr-4" alt="Buscar abogado de oficio" />,
              // }}
            />
          </Box>
        </div>
        <button
          onClick={() => createGlobalConfig(globalConfig)}
          className="flex justify-center items-center py-2 px-8 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-primary hover:bg-primary-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-400"
        >
          Guardar
        </button>
      </div>
      {/* table */}
      <div
        className={
          `grid grid-flow-col ml-27 mr-27 mt-4  w-auto rounded  p-0 relative ` +
          (isLoading ? 'h-60 bg-transparent' : 'bg-white title-box-shadow')
        }
      >
        {isLoading && <MainLoader />}
        {rows && (
          <Box>
            <DataGrid
              columns={columns}
              density="compact"
              onCellDoubleClick={(e: any) => handleOpenModal('edit', e.row)}
              editMode="row"
              rows={rows}
              autoHeight={true}
              pageSize={20}
              rowsPerPageOptions={[20]}
            />
          </Box>
        )}
      </div>
    </div>
  );
};
